    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    &::before {
        content: none;
    }
}

.slick-prev {
    background-image: url(../img/prev.svg);
    background-position: center;
    background-repeat: no-repeat;
}

.slick-next {
    background-image: url(../img/next.svg);
    background-position: center;
    background-repeat: no-repeat;
}

.slick-prev:hover,
.slick-prev:focus {
    outline: none;
    background-color: #FFFFFF;
    background-image: url(../img/prev.svg);
    background-position: center;
